
#zhuce{
	margin-top: 1rem;
	margin-bottom: 1rem;
	padding: 0.8rem;
	font-size: 0.7rem;
	color: #000;
}
#zhuce div:nth-of-type(1){
	margin-top: 0.4rem;
}
#zhuce div:nth-of-type(1) input{
	width: 100%;
	height: 1.7rem;
	color: #aaa;
	border: 1px solid #ccc;
	font-size: 0.65rem;
	text-indent: 0.4rem;
}
#zhuce div:nth-of-type(2){
	margin-bottom: 0.4rem;
}
#zhuce div:nth-of-type(2) input{
	width: 100%;
	height: 1.7rem;
	color: #aaa;
	border: 1px solid #ccc;
	font-size: 0.65rem;
	text-indent: 0.4rem;
}
#zhuce div:nth-of-type(3){
	margin-bottom: 0.6rem;
}
#zhuce h5{
	color: #DF5000;
	font-size: 0.6rem;
	font-weight: 100;
	text-align: center;
	margin-bottom: 0.4rem;
}
#zhuce div:nth-of-type(3) button{
	width: 100%;
	height: 1.7rem;
	color: #fff;
	border: 0;
	background: #17AA52;
	border-radius: 5px;
	font-size: 0.65rem;
	letter-spacing: 0.1rem;
}
#zhuce div:nth-of-type(4){
	margin-bottom: 1rem;
	font-size: 0.6rem;
	color: #aaa;
	text-align: center;
}
#zhuce div:nth-of-type(5){
	text-align: center;
	font-size: 0.8rem;
	color: #aaa;
}



