
#topchange{
	height: 1.5rem;
	line-height: 1.5rem;
	font-size: 0.65rem;
	width: 80%;
	position: absolute;
	left: 0;
	top: 1.45rem;
	z-index: 10;
	background: #fff;
	padding-left: 10%;
	padding-right: 10%;
	border-bottom: 1px solid #ccc;
}
#topchange span{
	display: inline-block;
	width: 49%;
	text-align: center;
}
#topchange .active{
	color:#55A532;
	border-bottom-color: #55A532;
}



