
@import '../../assets/font/movefont/iconfont.css';
#tab{
  	position: fixed;
    width: 100%;
    bottom: 0;
    border-top: 1px solid #ccc;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 1.5rem;
    font-size: 0.7rem;
    background: #fff;
}
#tab .tab-item{
  	width: 33%;
  	text-align: center;
  	line-height: 1.5rem;
}
#tab .tab-item span{
  	display: inline-block;
  	font-size: 0.7rem;
    color: #000;
}



