
#movie-commingsoon ul li{
	padding: 0.5rem;
	overflow: hidden;
	border-bottom: 1px solid #ccc;
	color: #777;
}
#movie-commingsoon ul li img{
	float: left;
	width: 4rem;
	margin-right: 1rem;
	-webkit-box-shadow: 3px 2px 2px #ccc;
	        box-shadow: 3px 2px 2px #ccc;
	border-radius: 5px;
}
#movie-commingsoon ul li .right{
	float: left;
}
#movie-commingsoon ul li .right h3{
	font-size: 0.75rem;
	line-height: 1.6rem;
	font-weight: 100;
	color: #000;
}
#movie-commingsoon ul li .right .star{
	line-height: 1rem;
}
#movie-commingsoon ul li .right .star span{
	color: #ccc;
}
#movie-commingsoon ul li .right .star span.active{
	color:orange;
}
#movie-commingsoon ul li .right .star i{
	color: #777;
}
#movie-commingsoon ul li .right .editor{
	line-height: 1rem;
}
#movie-commingsoon ul li .right .zhuyan{
	overflow: hidden;
	width: 9rem;
	margin-bottom: 0.4rem;
}
#movie-commingsoon ul li .right .zhuyan ol{
	padding:0 0.1rem;
	float: left;
}



