
@import './assets/css/base.css';
#loading{
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	z-index: 100000000;
	background: #fff;
}
#loading img{
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	right: 0;
	margin: auto;
	width: 80%;
}
.html-body-overflow
{
	height: 100vh;
  overflow-x:hidden;
  overflow-y:hidden;
}



