
#user{
	margin-top: 1.5rem;
	margin-bottom: 1.5rem;
}
#user .user_title{
	padding-top: 1rem;
	height: 6rem;
	background: url(bg.png) no-repeat 100% 100%;
	position: relative;
}
#user .user_title .mask{
	background: rgba(0,0,0,0.3);
	position: absolute;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
}
#user .user_title img{
	width: 5rem;
	height: 5rem;
	display: block;
	margin: 1rem auto;
	border-radius: 50%;
}
#user .name{
	text-align:center;
	line-height: 1.5rem;
	font-size: 0.8rem;
	border-bottom: 1px solid #ccc;
	position: relative;
}
#user .logout{
	position: absolute;
	right: 1rem;
	top: 0rem;
	font-size: 0.6rem;
	font-weight: 900;
}
#user .logout img{
	width: 1.5rem;
	vertical-align: middle;
}
#user .like .ilike{
	line-height: 1.5rem;
	font-size: 0.8rem;
	text-align: center;
	border-bottom: 1px solid #ccc;
}
#user .like ul li{
	padding: 0.5rem;
	overflow: hidden;
	border-bottom: 1px solid #ccc;
	color: #777;
}
#user .like ul li img{
	float: left;
	width: 4rem;
	margin-right: 1rem;
	-webkit-box-shadow: 3px 2px 2px #ccc;
	        box-shadow: 3px 2px 2px #ccc;
	border-radius: 5px;
}
#user .like ul li .right{
	float: left;
}
#user .like ul li .right h3{
	font-size: 0.75rem;
	line-height: 1.6rem;
	font-weight: 100;
	color: #000;
}
#user .like ul li .right .star{
	line-height: 1rem;
	position: relative;
}
#user .like ul li .right .star span{
	color: #ccc;
}
#user .like ul li .right .star span.active{
	color:orange;
}
#user .like ul li .right .star i{
	color: #777;
}
#user .like ul li .right .star strong{
	position: absolute;
	right: 1.5rem;
	top: 0rem;
	font-weight: 100;
	border: 1px solid #0086B3;
	border-radius: 5px;
	font-size: 0.8rem;
	padding: 0.2rem;
}
#user .like ul li .right .editor{
	line-height: 1rem;
}
#user .like ul li .right .zhuyan{
	overflow: hidden;
	width: 9rem;
	margin-bottom: 0.4rem;
}
#user .like ul li .right .zhuyan ol{
	padding:0 0.1rem;
	float: left;
}



