
#sousuo{
	height: 1.5rem;
	line-height: 1.5rem;
	font-size: 0.65rem;
	width: 70%;
	position: absolute;
	left: 0;
	top: 0;
	z-index: 100;
	background: #fff;
	padding-left: 20%;
	padding-right: 10%;
}
#sousuo input{
	width: 100%;
	height: 1rem;
	line-height: 1rem;
	text-indent: 0.5rem;
	border: 1px solid #ccc;
	border-radius: 0.5rem;
}



