
#rank{
	margin-top: 1rem;
	padding: 0.8rem;
	margin-bottom: 1rem;
}
#rank h2{
	line-height: 1.5rem;
	font-weight: 100;
}
#rank ul li{
	margin-bottom: 0.6rem;
	overflow: hidden;
	border-radius: 10px;
	height: 5.5rem;
}
#rank ul li:first-of-type{
	background:-webkit-gradient(linear, left top, left bottom, from(#e1a708), to(#efd491));
	background:linear-gradient(#e1a708, #efd491);
}
#rank ul li:nth-of-type(2){
	background:-webkit-gradient(linear, left top, left bottom, from(#32c05e), to(#a1e5be));
	background:linear-gradient(#32c05e, #a1e5be);
}
#rank ul li:nth-of-type(3){
	background:-webkit-gradient(linear, left top, left bottom, from(#a361c3), to(#d9b9e8));
	background:linear-gradient(#a361c3, #d9b9e8);
}
#rank ul li:nth-of-type(4){
	background:-webkit-gradient(linear, left top, left bottom, from(#dd7286), to(#f3b9c5));
	background:linear-gradient(#dd7286, #f3b9c5);
}
#rank ul li .left{
	width: 40%;
	height: 100%;
	padding-top: 1.8rem;
}
#rank ul li .left h3{
	font-weight: 100;
	font-size: 0.8rem;
	color: #fff;
	text-align: center;
}
#rank ul li .left p{
	font-size: 0.5rem;
	color: #fff;
	text-align: center;
}
#rank ul li .right{
	width: 60%;
	padding-top: 0.75rem;
}
#rank ul li .right img{
	width: 2.5rem;
	height: 3.5rem;
}
#rank ul li .right img:nth-of-type(2){
	width: 2.7rem;
	height:3.9rem;
	margin-bottom: -0.15rem;
}



